										url: '/pages/my/sigin'
									}
								]
							},
							{
								name: '快捷导航',
								sname: '',
								url: '#',
								subclass: 'all-url',
								subitem: [{
										name: '用户设置',
										icon: 'quanjushezhi',
										url: '/pages/my/account'
